//                                           Template tablette: - de 980px/760px
// -----------------------------------------------------------------------------
@media(max-width: 980px) {
  .blocAdresse {
    width: 32%;
    &:nth-of-type(4n){
      margin-right: .7rem;
    }
    &:nth-of-type(3n){
      margin-right: 0;
    }
  }
}
// ------------------------------------------------------------------------960px
@media(max-width: 960px) {
  .disno960 {
    display: none;
  }

  ul#tarifs, ul#tarifs2 {
    li {
      .micro a {
        font-size: .7rem;
        .micro-s {
          display: block;
          font-size: .5rem;
          font-family: $font-global;
        }
      }
    }
  }

  .metaAP {
    width: 250px;
  }

  .jotform-form .label-highlight {
    width: 25%;
  }
  .jotform-form input.form-textbox,
  .jotform-form input.form-textbox2 {
    width: 30%;
  }

  //----------------------------------------------------------------------------
  //                           PAGE PAIEMENTS
  //----------------------------------------------------------------------------

  #paiements_left,
  #valid_bat_left {
    width: 100%;
  }

  .paiements_right_con {
    width: 49%;
  }

  .checkbox2 {
    width: 80%;
    margin-top: -5px;
    line-height: .8rem;
  }
  #suivant_reg {
    width: 20%;
  }
  // ---------------------------------------------------------------------------
  //                                POP UP / MODALS
  // ---------------------------------------------------------------------------

  .modal {
      width: 95%;
      padding: 1rem;
  }
  .modal h1 {
      font-size: 1rem;
  }
  a.notech,a.notech1 {
    width: 30px;
  }
  // ---------------------------------------------------------------------------

  .disno960{
    display: none;
  }
}

//-------------------------------------------------------------------------760px
@media(max-width: 760px) {

  // invisible -----------------------------------------------------------------

  .disno760,
  .textHide,
  .otip {
    display: none;
  }
  .blocPromo {
    width: 100%;
  }
  //----------------------------------------------------------------------------
  .blocAdresse {
    width: 48%;
    &:nth-of-type(4n){
      margin-right: .7rem;
    }
    &:nth-of-type(3n){
      margin-right: .7rem;
    }
    &:nth-of-type(2n){
      margin-right: 0;
    }
  }


  //----------------------------------------------------------------------------
  //                            PAGE ACCUEIL
  //----------------------------------------------------------------------------

  // icones --------------------------------------------------------------------

  ul#tarifs, ul#tarifs2 {
    li {
      width: 32%; //120
    }
  }

  // slider
  h1.metaTitle {
    margin-bottom: 0 !important;
  }
  .metaDesc {
    display: none;
  }
  .metaAP {
    width: 160px;
  }
  .metaPrice {
    font-size: 3rem !important;
    line-height: 3rem !important;
  }

  a.metaButton {
    width: 100px;
    height: 35px;
    font-size: 1.4rem !important;
  }

  ul#introBlocs {
    li {
      width: 49%; //120
      margin-bottom: 20px;
    }
  }
  //----------------------------------------------------------------------------
  //                          PAGES PRODUITS
  //----------------------------------------------------------------------------

  #top_info {
    .front img{
      @include span(4);
      vertical-align: bottom;
      max-height: auto;
    }
    #top_info_info {
      @include span(8);
    }
  }

  #buying{
    min-height: 450px;
  }

  #preview,
  #preview2,
  #preview3 {
    min-height: 350px;
    .arrow {
      height: 50px;
      top: 30px;
    }
  }

  .pushButton{
    margin-left:0;
  }

  // afficher à gauche les tooltips aide sélection produits --------------------

  .helpText {
    left: auto;
    right: 35px;
    width: 280px;
  }

  .helpText:after, .helpText:before {
    left: 100%;
    top: 28px;
  }
  .helpText:after {
  	border-color: rgba(246, 246, 246, 0);
  	border-left-color: #f6f6f6;
  	border-width: 10px;
  	margin-top: -10px;
  }
  .helpText:before {
  	border-color: rgba(233, 233, 233, 0);
  	border-left-color: #e9e9e9;
  	border-width: 11px;
  	margin-top: -11px;
  }

  .helpPage1 {
    font-size: 3rem;
  }

  h1.helpPageTitle {
    font-size: 1rem;
  }

  // largeur des champs select -------------------------------------------------

  ul.form-section li.optionsformline4,
  ul.form-section li.optionsformline5, {
    width: 100%;
  }

  ul.form-section {

    li.optionsformline,
    li.optionsformlineOld {
      .options_single span {
        &.optionsleft,
        &.optionsright {
          width: 100%;
            .helpButton {
            right: 5px;
            left: auto;
          }
        }
      }
    }
  }

  // tableau ratings -----------------------------------------------------------
  #rating_general {
    width: 100%;
    margin-bottom: 50px;
  }

  a.comVoir  {
    width: 100%;
    display: block;
    .comVoirContent {
      display: block;
      position: absolute;
      top: 8px;
      left: 20px;
    }
  }

  #fbcart_rating {
    position: relative;
    display: inline-block;
    width: 100%;
    border-bottom: 0;
    background-image: none;
    tr{
      outline: 1px solid #e9e9e9;
    }

    tr:after, tr:before {
      top: -10px;
      z-index: 100;
    	left: 10%;
    	border: solid transparent;
    	content: " ";
    	height: 0;
    	width: 0;
    	position: absolute;
    	pointer-events: none;
    }

    tr:after {
      border-color: rgba(246, 246, 246, 0);
	    border-bottom-color: #f6f6f6;
    	border-width: 15px;
    	margin-left: -15px;
    }

    tr:before {
      border-color: rgba(233, 233, 233, 0);
	    border-bottom-color: #e9e9e9;
      border-bottom: none;
    	border-width: 16px;
    	margin-left: -16px;
    }

    td {
      display: block;
      width: 100%;

      &.lefttd {
        border-bottom: 0;
        display: block;
        width: 100%;
      }

      &.lefttd2 {
        display: block;
        width: 100%;
        border-bottom: 0;
        padding:0 .7rem;
        background-image: none;
      }
    }
  }
  // tableau PLV ---------------------------------------------------------------

  #promotions_table {
    tr {
      display: block;
      background: $bloc-base;
      margin-bottom: 20px;
    }
    td {
      display: inline-block;
      width: 49%;
      border-bottom: 0;
      &.lefttd{
        background: transparent;
      }
      &.optionstd {
        width: 49%;
        border-bottom: 0;
        background: transparent;
      }

      &.righttd {
        width: 49%;
        border-bottom: 0;
        background: transparent;
      }
    }
  }

  // autres éléments de pages wordpress

  .bloc-left,
  .bloc-right{
    width: 100%;
    float: none;
    margin: 0;
  }

  //----------------------------------------------------------------------------
  //                       PAGES ACCES CLIENT
  //----------------------------------------------------------------------------

  .acces_left {
    float: none;
    display: inline;
    @include span(12);
  }

  .acces_right {
    float: none;
    display: inline;
    @include span(12);
  }

  // tableaux devis ------------------------------------------------------------

  #fbcart_address {
    width: 100%;
  }

  #fbcart_check {
    width: 100%;
  }


  //----------------------------------------------------------------------------
  //                           PAGE CONTACT
  //----------------------------------------------------------------------------

  #contact1 {
    width: 100%;
  }
  #contact2 {
    width: 100%;
    margin-left: 0;
  }
  #contact3 {

    width: 100%;
  }
  #contact4 {
    margin-left: 0;
    width: 100%;
  }

  span.contacter,
  span.contacter2 {
    width: 100%;
  }

  // boutons à 50% de l'écran --------------------------------------------------
  .fuselect,
  .fustart,
  .fucancel,
  .fudelete,
  a.but_voiremaquette,
  a.but_vumaquette,
  #but_exporter,
  #but_modifier,
  #but_retour,
  #but_imprimer,
  #but_imprimerfacture,
  #but_ajouter,
  #but_comment,
  #but_suivre,
  #but_rating,
  #but_supprimer,
  #but_annuler,
  #validerdevis,
  #but_continuer,
  a#but_imprimerbon,
  #but_imprimer_rib,
  form#delfromvosdevis,
  #paye,
  #but_annulercommande.deactive {
    width: 49%;
  }



  // réduction hauteur des boutons annuler/effacer du module upload

  .fucancel,
  .fudelete {
    height: 25px;
    padding: 0;
  }

  // ajustements padding top status commandes
  .comment_new {

  }


  // ---------------------------------------------------------------------------
  //                            FOOTER
  // ---------------------------------------------------------------------------

  #derniers {
    margin-top: 0;
    @include span(12);
    padding-right: 0;

  }

  #banners {
    @include span(12);
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 20px;
    .banner1 {
      width: 100%;
    }
    .banner2 {
      margin-top: 15px;
      width: 100%;
    }
  }

  h4.clients_reviews_titre {
    margin-top: 0;
  }

  //----------------------------------------------------------------------------
  //                                   MODALS
  //----------------------------------------------------------------------------
  #explode{
    .explode{
      display: block;
      width: 33%;
    }
  }

}
